<template>
    <section class="child">
        <div class="z-0 root animate__animated animate__fadeIn">
            <div
                class="z-0 w-full h-screen overflow-x-hidden"
                id="tsparticles"
                ref="background"
            ></div>
            <div class="font-bold section section-1 -height" ref="first">
                <div>
                    <div class="inline">
                        I like
                    </div>
                    <div class="inline text-nord8">
                        {{ text }}
                    </div>
                    <div
                        class="inline border-r-2 border-gray-600 cursortext"
                    ></div>
                    <div class="inline">.</div>
                </div>
            </div>

            <button
                class="z-10 flex items-end justify-center wrap"
                @click.prevent="scrollDown"
            >
                <img
                    src="http://image.flaticon.com/icons/svg/3/3907.svg"
                    id="arrow"
                    class="animated bounce"
                />
            </button>
        </div>
    </section>
</template>

<script>
import { ref, onMounted } from 'vue';
import { tsParticles } from 'tsparticles';
export default {
    components: {},
    setup() {
        const likes = [
            'Programming',
            'Cubing',
            'Basketball',
            'Vue.js',
            'Homebrew',
            'Jailbreaking',
            'Photography',
            'undefined',
        ];
        if (Math.random() < 0.1) {
            likes.push('https://www.github.com/Ridhwaan001');
        }
        const text = ref('');

        // typewriter stuff
        function typewriter(i) {
            const word = likes[i > likes.length - 1 ? 0 : i];
            addletter([...word], 0, i > likes.length - 1 ? 0 : i);
        }
        function addletter(word, i, typeIndex) {
            setTimeout(() => {
                text.value += word[i];
                if (i + 1 < word.length) {
                    addletter(word, i + 1, typeIndex);
                } else {
                    setTimeout(() => {
                        removeletter(typeIndex);
                    }, 1000);
                }
            }, 150);
        }
        function removeletter(i) {
            setTimeout(() => {
                text.value = text.value.slice(0, -1);
                if (text.value.length > 0) {
                    removeletter(i);
                } else {
                    typewriter(i + 1);
                }
            }, 75);
        }

        function initParticles() {
            tsParticles.load('tsparticles', {
                particles: {
                    number: {
                        value: 80,
                        density: {
                            enable: false,
                            value_area: 800,
                        },
                    },
                    color: {
                        value: '#88c0d0',
                    },
                    shape: {
                        type: 'circle',
                        stroke: {
                            width: 0,
                            color: '#d8dee9',
                        },
                        polygon: {
                            nb_sides: 3,
                        },
                        image: {
                            src: '',
                            width: 0,
                            height: 0,
                        },
                    },
                    opacity: {
                        value: 0.5,
                        random: false,
                        anim: {
                            enable: false,
                            speed: 1,
                            opacity_min: 0.1,
                            sync: false,
                        },
                    },
                    size: {
                        value: 3,
                        random: true,
                        anim: {
                            enable: false,
                            speed: 40,
                            size_min: 0,
                            sync: false,
                        },
                    },
                    line_linked: {
                        enable: true,
                        distance: 150,
                        color: '#88c0d0',
                        opacity: 0.4,
                        width: 2.0517838682439087,
                    },
                    move: {
                        enable: true,
                        speed: 2,
                        direction: 'none',
                        random: false,
                        straight: false,
                        out_mode: 'out',
                        bounce: false,
                        attract: {
                            enable: false,
                            rotateX: 600,
                            rotateY: 1200,
                        },
                    },
                },
                interactivity: {
                    detect_on: 'canvas',
                    events: {
                        onhover: {
                            enable: false,
                            mode: 'grab',
                        },
                        onclick: {
                            enable: false,
                            mode: 'push',
                        },
                        resize: true,
                    },
                    modes: {
                        grab: {
                            distance: 400,
                            line_linked: {
                                opacity: 1,
                            },
                        },
                        bubble: {
                            distance: 400,
                            size: 40,
                            duration: 2,
                            opacity: 8,
                            speed: 3,
                        },
                        repulse: {
                            distance: 200,
                            duration: 0.4,
                        },
                        push: {
                            particles_nb: 4,
                        },
                        remove: {
                            particles_nb: 2,
                        },
                    },
                },
                retina_detect: true,
            });
        }

        onMounted(() => {
            initParticles();
            typewriter(0);
        });

        function scrollDown() {
            window.scrollBy(0, window.innerHeight - window.pageYOffset + 25);
        }

        return {
            text,
            scrollDown,
        };
    },
};
</script>

<style lang="stylus" scoped>
