    overflow-x hidden
}
.background {
	top: 0;
	left: 0;
    z-index -1
    background-color #2e3440
}

.section {
	min-height: 100vh;
	position: relative;
    color white
    display flex
    justify-content center
    align-items center
}

.section-1 {
    font-size: 4em;
}

.cursortext {
    animation: blinker 1s step-end infinite;
}

@keyframes blinker {
    50% {
      opacity: 0;
    }
}


.wrap {
    img {
        filter: invert(87%) sepia(4%) saturate(4150%) hue-rotate(162deg) brightness(86%) contrast(88%);
    }
}

.bounce {
    animation-iteration-count: infinite;
    animation-duration: 1.5s;
    animation: bounce 3s ease infinite;
    transform-origin: 50% 50%;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  5.55556% {
    transform: translateY(0);
  }
  11.11111% {
    transform: translateY(0);
  }
  22.22222% {
    transform: translateY(-15px);
  }
  27.77778% {
    transform: translateY(0);
  }
  33.33333% {
    transform: translateY(-15px);
  }
  60% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0);
  }
}
</style>
