<template>
    <div id="container">
        <parallax-home />
        <phone-link />
        <not-reddit />
        <edulink />
        <credits />
    </div>
</template>

<script>
import { onMounted } from 'vue';

import parallaxHome from '@/components/main/parallaxHome';
import phoneLink from '@/components/main/phoneLink';
import notReddit from '@/components/main/notReddit';
import edulink from '@/components/main/edulink';
import credits from '@/components/main/credits';

export default {
    components: {
        parallaxHome,
        phoneLink,
        notReddit,
        credits,
        edulink,
    },
    setup() {
        onMounted(() => {
            window.addEventListener('scroll', e => {
                let scrolled = window.pageYOffset;
                {
                    const target = document.querySelectorAll('.parallaxX');
                    for (const t of target) {
                        t.style.transform = `translate3d(${(scrolled -
                            t.offsetTop) *
                            (t.dataset.scrollspeed / 10)}px, 0px, 0px)`;
                    }
                }
                {
                    const target = document.querySelectorAll('.parallaxZ');
                    for (const t of target) {
                        t.style.transform = `translate3d(0px, ${(scrolled -
                            t.offsetTop) *
                            (t.dataset.scrollspeed / 10)}px, 0px)`;
                    }
                }

                setTimeout(() => {
                    if (scrolled == window.pageYOffset) {
                        if (
                            scrolled % window.innerHeight >=
                            window.innerHeight / 2
                        ) {
                            window.scrollBy(
                                0,
                                window.innerHeight -
                                    (scrolled % window.innerHeight),
                            );
                        } else {
                            window.scrollBy(
                                0,
                                -(scrolled % window.innerHeight),
                            );
                        }
                    }
                }, 750);
            });
        });
    },
};
</script>

<style lang="stylus">

body::-webkit-scrollbar {
    width: 7px;
}

body::-webkit-scrollbar-track {
    opacity 0
	background-color: #2e3440
}

body::-webkit-scrollbar-thumb {
    background-color: #434c5e
	border-radius 1rem
}
</style>
